* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: darken($clr3, 10%);
    color: $clr5;
}

.app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;

}

main {
    flex: 1 1 auto;
    // height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    display: inline-block;
    text-decoration: none;
    color: inherit;
}

img {
    width: 100%;
    // height: 100%;
    // object-fit: cover;
}


