.toy-filter {
    padding-block-start: $layoutMainPadding;

    & > * {
        padding-block-end: $layoutMainPadding;
    }


    form {
        .main-input {
            display: flex;
            gap: calc($layoutSmallPadding * 2);
            padding-block-end: $layoutMainPadding;
        }

        .label-selector {
            display: grid;
            grid-template-columns: 150px 150px 150px 150px;
            grid-template-rows: 20px 20px;
            column-gap: $layoutMainPadding;
            padding-block: $layoutMainPadding;
        }
    }
}