.toy-review {
    padding-block: $layoutMainPadding;

    .review-preview {
        display: list-item;
        align-items: center;
        gap: $layoutMainPadding;
        padding-block-end: $layoutMainPadding;

        .custom-button-remove {
            @include custom-button($clr6);
            margin-inline-end: $layoutMainPadding;
        }

        span {
            font-weight: 100;
            font-size: 0.7em;
            margin-inline-end: $layoutMainPadding;
        }
    }
}